import { _decorator, Component, instantiate, Node, Prefab, randomRange, Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass("EnemyManager")
export class EnemyManager extends Component {
  @property(Prefab) enemyPrefab: Prefab = null;
  @property(Number) poolSize: number = 10;
  @property(Number) spawnRangeX: number = 20;
  @property(Number) spawnRangeY: number = 10;

  spawnDepth : number = 0;

  private enemyPool: Node[] = [];

  protected onLoad(): void {
    this.initializeEnemyPool();
  }

  private initializeEnemyPool() {
    for (let i = 0; i < this.poolSize; i++) {
      const enemy = instantiate(this.enemyPrefab);
      enemy.active = false;
      this.node.addChild(enemy);
      this.enemyPool.push(enemy);
    }
    console.log(this.enemyPool);
  }

  spawnEnemy(){
    
  }
  
}
